import CONSTANTS from './HomeCarouselConstants';
import { fromJS } from 'immutable';

export default function (state, action) {
  switch (action.type) {
    case CONSTANTS.FETCH:
      return fromJS(action.payload);
    default:
      return state;
  }
}



