import CONSTANTS from './HomeCarouselConstants';
import Repository from './HomeCarouselRepository';

export default class ProductActions {
  constructor(tenant) {
    this.repository = new Repository(tenant);
  }
  fetch() {
    return {
      type: CONSTANTS.FETCH,
      payload: this.repository.fetch(),
    };
  }
}



