if (typeof require.ensure !== 'function') {require.ensure = (d, c) => { c(require); }; }
if (typeof require.include !== 'function') { require.include = () => {}; }
module.exports = {
  ofTenant: tenant => {
    return [
      {
        path: 'detail/:id',
        getComponent: (location, cb) => {
          require.ensure([], require => {
            const Product = require('v02/front-office/modules/home/home-carousel/detail').default.get(tenant);
            cb(null, Product);
          });
        },
      },
      {
        path: 'disclaimer',
        getComponent: (location, cb) => {
          require.ensure([], require => {
            const Product = require('v02/front-office/modules/disclaimer/components/disclaimer').default.get(tenant);
            cb(null, Product);
          });
        },
      },
      {
        path: 'domain/:alias',
        getComponent: (location, cb) => {
          require.ensure([], require => {
            const Thematic = require('v02/front-office/modules/item-groups/domain').default.get(tenant);
            cb(null, Thematic);
          });
        },
      },
      {
        path: 'product/:alias',
        getComponent: (location, cb) => {
          require.ensure([], require => {
            const Product = require('v02/front-office/modules/products/components/front-product-sheet').default.get(tenant);
            cb(null, Product);
          });
        },
        childRoutes: require('v02/front-office/modules/products/ProductRoutes').ofTenant(tenant),
      },
      {
        path: 'user-corner/:alias',
        getComponent: (location, cb) => {
          require.ensure([], require => {
            const Product = require('v02/front-office/modules/item-groups/user-corner').default.get(tenant);
            cb(null, Product);
          });
        },
      },
      {
        path: 'bundle/:alias',
        getComponent: (location, cb) => {
          require.ensure([], require => {
            const Bundle = require('v02/front-office/modules/bundles/components/front-bundle').default.get(tenant);
            cb(null, Bundle);
          });
        },
      },
      {
        path: 'catalog*',
        getComponent: (location, cb) => {
          require.ensure([], require => {
            const Product = require('v02/front-office/modules/item-groups/all').default.get(tenant);
            cb(null, Product);
          });
        },
      },
      // TODO: To Refactor
      // todo: remove this and use split to make filters more dynamic later on
      {
        path: 'catalog/keywords/:tags',
        getComponent: (location, cb) => {
          require.ensure([], require => {
            const Product = require('v02/front-office/modules/item-groups/all').default.get(tenant);
            cb(null, Product);
          });
        },
      },
    ];
  },
};



